(function () {
  let elems = document.querySelectorAll('[data-bigNewsUppercase]');
  elems.forEach(elem => {
    let wordsArray = elem.textContent;
    const editedWords = wordsArray.replace(/(^|\s)\S/g, function (a) { return a.toUpperCase() });
    let h3 = document.createElement('h3');
    h3.className = 'big-news__newstitle';
    h3.innerHTML = editedWords;
    elem.replaceWith(h3);
  });

})();

