const icon = document.querySelector('.info-block__search-loop');
const search = document.querySelector('.info-block__search-header');
const clear = document.querySelector('.info-block__clear');

icon.onclick = function () {
  search.classList.add('active');

}

clear.onclick = function () {
  document.getElementById('header-search').value = '';
  search.classList.toggle('active');
}
