(function () {

  const scrollBtn = document.querySelector('.scollToTop');

  window.onscroll = () => {
    if (window.scrollY > 500) {
      scrollBtn.classList.remove('isShowBtn_hide')
    } else if (window.scrollY < 500) {
      scrollBtn.classList.add('isShowBtn_hide')
    }
  }

  scrollBtn.onclick = () => {
    window.scrollTo(0, 0);
  }
})();
